@import url(https://fonts.googleapis.com/css?family=Abril+Fatface|Oleo+Script|Signika|Nunito:300,400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Title-container {
  max-width: 1000px;
  margin: auto;
  text-align: center; }

.Title {
  height: 370px;
  min-height: 300px;
  max-width: 1000px;
  padding: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  vertical-align: middle;
  flex-direction: column; }

.Title-name h1 {
  font-family: Oleo Script;
  color: #2A363B;
  margin: auto; }

.Title-name h2 {
  font-family: Nunito, display;
  font-size: 1.7rem;
  font-weight: 300; }

.Title-about {
  margin: auto;
  max-width: 700px;
  margin-top: 3rem;
  font-family: Nunito, display;
  font-size: 1.7rem;
  font-weight: 300;
  font-size: 1.25rem; }

.Home {
  background-color: #2A363B;
  border: 0.5rem solid #2A363B;
  border-top: none; }

.Home-container {
  background-color: #FAFAFA;
  height: 100vh; }

.Navbar {
  background-color: #FAFAFA;
  border: 0.5rem solid #2A363B;
  border-bottom: none; }

.Navbar-container {
  max-width: 1000px;
  margin: auto;
  display: flex;
  flex-direction: row;
  padding: 2.5rem; }

.Navbar-avatar-container {
  width: 75px;
  height: 75px;
  overflow: hidden;
  margin-right: auto;
  border-radius: 3px; }
  .Navbar-avatar-container img {
    max-height: 100px; }

.Navbar-link-container {
  margin-top: auto; }
  .Navbar-link-container a {
    text-decoration: none;
    color: #2A363B;
    font-family: Nunito, display;
    font-weight: 500;
    font-size: 1.2rem;
    margin-left: 1rem; }

* {
  margin: 0;
  padding: 0; }

h1 {
  font-size: 72px;
  font-weight: 400; }

